/* Base */
:root {
  --bg1: #f8fafc;
  --bg2: #eef2f7;
  --card: #ffffff;
  --ink: #0f172a;
  --sub: rgba(15,23,42,.6);
  --accent: #10b981;
  --accent-2: #0ea5e9;
  --warn-bg: #fef3c7;
  --warn-ink: #92400e;
  --muted: #e5e7eb;
}

* { box-sizing: border-box; }
body { margin: 0; background: linear-gradient(to bottom, var(--bg1), var(--bg2)); color: var(--ink); font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial; }

.app { min-height: 100vh; padding: 24px; }
.container { max-width: 1100px; margin: 0 auto; display: flex; flex-direction: column; gap: 16px; }

/* Header */
.header { display: flex; align-items: center; justify-content: space-between; }
.title { font-weight: 800; letter-spacing: -0.02em; font-size: 28px; }
.round { font-size: 12px; opacity: .7; }

/* Card-like panels */
.panel { background: var(--card); border-radius: 18px; box-shadow: 0 8px 24px rgba(0,0,0,.08); padding: 16px; }

/* Setup form */
.setup { display: grid; grid-template-columns: 1fr 1fr; gap: 16px; }
.setup .group { display: flex; flex-direction: column; gap: 8px; }
.input { border: 1px solid var(--muted); border-radius: 12px; padding: 10px; font-size: 14px; }
.row { display: flex; gap: 8px; }
.btn { border: 0; border-radius: 12px; padding: 10px 14px; cursor: pointer; background: #111827; color: #fff; }
.btn.primary { background: var(--accent); }
.btn.secondary { background: #e5e7eb; color: #111827; }
.btn.info { background: var(--accent-2); }
.btn:disabled { opacity: .5; cursor: not-allowed; }

/* Scoreboard chips */
.score { display: flex; flex-wrap: wrap; gap: 8px; }
.chip { background: var(--card); border-radius: 999px; padding: 6px 10px; box-shadow: 0 4px 12px rgba(0,0,0,.08); display: flex; align-items: center; gap: 8px; }
.chip .dot { width: 8px; height: 8px; border-radius: 999px; background: #cbd5e1; }
.chip.active { background: #ecfdf5; }
.chip.active .dot { background: var(--accent); }
.chip .pts { font-size: 12px; opacity: .7; }

/* Main layout: center board + right sidebar */
.layout { display: grid; grid-template-columns: 1fr 260px; gap: 16px; align-items: start; }

/* Piles row (above grid) */
.piles { display: flex; align-items: center; justify-content: center; gap: 16px; margin-bottom: 12px; }
.pile { width: 100px; height: 140px; border: 2px solid #111827; border-radius: 14px; background: #fff; display: flex; flex-direction: column; align-items: center; justify-content: center; gap: 4px; cursor: pointer; transition: transform .05s ease, box-shadow .15s ease; }
.pile:not(.disabled):hover { box-shadow: 0 8px 16px rgba(0,0,0,.12); transform: translateY(-1px); }
.pile.disabled { opacity: .5; cursor: not-allowed; }
.pile-value { font-size: 28px; font-weight: 700; user-select: none; }
.pile-label { font-size: 11px; text-transform: uppercase; letter-spacing: .06em; opacity: .6; }

.preview { width: 80px; height: 120px; border: 2px dashed #334155; border-radius: 10px; display: flex; align-items: center; justify-content: center; background: #f8fafc; font-weight: 700; }
.preview-empty { opacity: .5; }

/* Player center area */
.center { background: var(--card); border-radius: 18px; box-shadow: 0 8px 24px rgba(0,0,0,.08); padding: 16px; }
.center .who { text-align: center; margin-bottom: 8px; }
.center .who .name { font-size: 20px; font-weight: 700; }
.center .who .sub { font-size: 12px; opacity: .6; }
.badge { font-size: 11px; display: inline-block; padding: 4px 8px; border-radius: 8px; background: var(--warn-bg); color: var(--warn-ink); margin-top: 6px; }

/* Grid of cards (3x4) */
.grid { display: grid; grid-template-columns: repeat(4, 86px); grid-auto-rows: 126px; gap: 10px; justify-content: center; }
.cell { width: 86px; height: 126px; border: 2px solid #334155; border-radius: 12px; display: flex; align-items: center; justify-content: center; font-weight: 800; font-size: 28px; background: #ffffff; cursor: pointer; user-select: none; box-shadow: 0 2px 6px rgba(0,0,0,.08); }
.cell.face-down { background: #475569; color: transparent; }
.cell.removed { visibility: hidden; }
.cell.highlight { outline: 4px solid rgba(16,185,129,.35); }

/* Actions row */
.actions { margin-top: 12px; display: flex; flex-wrap: wrap; gap: 8px; justify-content: center; }
.hint { margin-top: 6px; font-size: 12px; opacity: .6; text-align: center; }

/* Sidebar */
.sidebar .title { font-size: 13px; font-weight: 700; margin-bottom: 8px; }
.order { list-style: none; padding: 0; margin: 0; display: flex; flex-direction: column; gap: 6px; }
.order li { display: flex; align-items: center; justify-content: space-between; }
.order .name { display: flex; align-items: center; gap: 8px; }

/* Round / Game over */
.center .table { margin: 12px auto 0; border-collapse: collapse; }
.center .table th, .center .table td { padding: 6px 10px; border-bottom: 1px solid #e5e7eb; text-align: left; }

/* Footer */
.footer { margin-top: 16px; font-size: 12px; opacity: .6; text-align: center; }
